import React, { useEffect, useState } from 'react'
import { GoogleMap, useJsApiLoader, Marker, InfoWindow } from '@react-google-maps/api';

const containerStyle = {
  width: '100%',
  height: '100%'
};

const center = {
  lat: -3.361378,
  lng: 29.366667
};


const libraries = ["places"];

function EmbeddedMap({ onChange = () => { }, coordinatesBuilder = (index) => { }, markerCount = 0 }) {
  const { isLoaded } = useJsApiLoader({
    id: 'google-map-script',
    libraries: libraries,
    googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAP_KEY,

  })

  const onLoad = React.useCallback(function callback(map) {
    const bounds = new window.google.maps.LatLngBounds(center);
    map.fitBounds(bounds);
    setMap(map)
  }, [])

  const onUnmount = React.useCallback(function callback(map) {
    setMap(null)
  }, [])

  const [selectedPosition, setSelectedPosition] = useState(null);

  const [map, setMap] = React.useState(null);

  const [markers, setMarkers] = React.useState([]);

  const [hasSetMarkers, sethasSetMarkers] = React.useState(false);

  useEffect(() => {
    if (!hasSetMarkers) {
      sethasSetMarkers(true)
      const arr = Array.from({ length: markerCount }, (_, i) => {
        const coordinates = coordinatesBuilder(i);
        return coordinates;
      });
      setMarkers(arr);
    }


    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [coordinatesBuilder, markerCount])

  return isLoaded ? (
    <GoogleMap
      mapContainerStyle={containerStyle}
      center={selectedPosition ? { lat: selectedPosition.lat, lng: selectedPosition.lng } : markerCount ? { lat: parseFloat(markers[0].lat), lng: parseFloat(markers[0].lng) }: center}
      zoom={9}
      onLoad={onLoad}
      onUnmount={onUnmount}
      onClick={(event) => {
        console.log(map ?? '');
        onChange(event.latLng.lat(), event.latLng.lng());
      }}
    >
      <>
        {markers.map((coordinates, i) => {
          return <Marker key={i}
            position={new window.google.maps.LatLng(parseFloat(coordinates.lat), parseFloat(coordinates.lng))}
            onClick={(event) => {
              setSelectedPosition({ lat: event.latLng.lat(), lng: event.latLng.lng() });
            }}
          />
        })}
        {
          selectedPosition ? <>
            <InfoWindow
              position={new window.google.maps.LatLng(parseFloat(selectedPosition.lat), parseFloat(selectedPosition.lng))}
              onCloseClick={() => {
                setSelectedPosition(null);
              }}>
              <div />
            </InfoWindow>
          </> : <></>
        }
      </>
    </GoogleMap>
  ) : <></>
}

export default React.memo(EmbeddedMap)

