
import React from "react";
import { connect } from "react-redux";
import { systemAuthBaseUrl } from "../../../config/config";
import ApiProvider, { ApiMethods } from "../../../data/providers/api_services";
import { getErrorMessage, isEmpty } from "../../../utils/utils";
import Loader from "../../components/loader/loader";
import Notifier from "../../components/notifier/notifier";
import RawTitleValue from "../../components/title_value/title_value";



class BankTranserPayment extends React.Component {

    state = {
        hasAgreed: false,
        bankInformation: {},
        referenceNumber: '',
        isAsyncCall: false,
        errorMessage: '',
    }
    render() {
        return <div className="steps_holder">
            <p>How to pay by Bank tranfer ? </p>
            {!isEmpty(this.state.errorMessage) ? <Notifier type='error' message={this.state.errorMessage} /> : <></>}
            <div className="steps_holder_b_transfer">
                <p>To activate your subscription, please transfer the sum of <span>${this.props.SubscriptionType.Price}</span> to our payment agent<span>{this.state.hasAgreed && ' listed below'}</span>, quoting as the name of the sender the reference number <span className={`${this.state.hasAgreed && 'refCodeP'}`}>{this.state.hasAgreed ? `${this.state.referenceNumber}` : ' which will be generated'}</span>. The reference number is valid for 24 hours, at which time payment is expected. If the reference number is not included in the notes with the transfer, we will not be able to activate your subscription. This includes the irreversibility of the operation, hence the loss of your money.</p>
                <div className="divider" />
                {!this.state.hasAgreed
                    ? <> {this.state.isAsyncCall
                        ? <div className="full_space_loader"><Loader /></div>
                        : <div className="accept_bank_transfer_conditions" onClick={(event) => {
                            event.stopPropagation();
                            this.onRequestingPaimentIntent();
                        }}>
                            <p>I have read and agree to pay by Bank transfer</p>
                            <i className="fa-solid fa-arrow-right-long" />
                        </div>
                    }
                    </>
                    : <>
                        <RawTitleValue title='Your Reference No' value={`${this.state.referenceNumber}`} />
                        <RawTitleValue title='Bank name' value={`${this.state.bankInformation.BankName}`} />
                        <RawTitleValue title='City' value={`${this.state.bankInformation.City}`} />
                        <RawTitleValue title='SWIFT Code' value={`${this.state.bankInformation.SwiftCode}`} />
                        <RawTitleValue title='IBAN' value={`${this.state.bankInformation.Iban}`} />
                        <RawTitleValue title='Payee' value={`${this.state.bankInformation.Payee}`} />
                        <RawTitleValue title='Bank Account Number' value={`${this.state.bankInformation.BankAccountNumber}`} />
                        <RawTitleValue title='Country' value={`${this.state.bankInformation.Country}`} />
                    </>}
            </div>
        </div>
    }

    onRequestingPaimentIntent() {
        this.setState({ isAsyncCall: true, errorMessage: '' });
        this.props.isAsync(true);
        const restApiServices = new ApiProvider();
        restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: 'api/payment/bank-transfer-create-intent',
            apiMethod: ApiMethods.post,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: this.props?.userInformation?.accessToken,
                },
                body: {
                    SubscriptionTypeId: this.props.SubscriptionType.id,
                }
            },
        }).then((response) => {
            if (typeof (response.error) === 'undefined') {
                console.log(response.response);
                const { CodeRef, BankName, Country, City, SwiftCode, Payee, BankAccountNumber, Iban } = response.response;
                this.props.isAsync(false);
                this.setState({
                    isAsyncCall: false, hasAgreed: true, referenceNumber: CodeRef, bankInformation: {
                        BankName: BankName,
                        Country: Country,
                        City: City,
                        SwiftCode: SwiftCode,
                        Payee: Payee,
                        BankAccountNumber: BankAccountNumber,
                        Iban: Iban,
                    }
                });
            } else {
                this.props.isAsync(false);
                this.setState({ isAsyncCall: false, errorMessage: getErrorMessage('e13', 'en') });
            }
        });
    }
}

const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage,
    userInformation: storeOf.madoshome.userInformation,
    subscriptionPlans: storeOf.madoshome.subscriptionPlans,
});

export default connect(mapStateToProps)(BankTranserPayment);