import React from "react";
// import dummyData from "../../../utils/fake_data";
import CardsDisplayer from "../cards_displayer/cards_displayer";
// import ShimmerEffectsUi from "../shimmers_effects/shimmers_effects";
// import ShimmerCards from "../shimmer_cards/shimmer_card";
import './entity_displayer.css';



export default class EntityDisplayer extends React.Component {

    state = {
        isAsyncCall: true,
        isAsyncMore: false,
        entities: [],
        maxReached: false,
    }

    max = 40;


    loading() {
        if (!this.state.isAsyncMore) {
            this.setState({ isAsyncMore: true });
            this.props.onFetchingEntities(this.state.entities.length ? this.state.entities[this.state.entities.length - 1].id : 0).then((entities) => {
                if ([...entities].length) {
                    if (this.state.entities.length < this.max) {
                        this.setState({ entities: [...this.state.entities, ...entities], isAsyncMore: false, isAsyncCall: false });
                    } else {
                        this.setState({ entities: entities, isAsyncMore: false, isAsyncCall: false });
                    }
                } else {
                    this.setState({ maxReached: true, isAsyncMore: false, isAsyncCall: false });
                }
            });
        }
    }


    componentDidMount() {
        if ([...this.props.entities].length) {
            this.setState({ isAsyncCall: false, entities: [...this.props.entities] });
        } else {
            if (this.props.onFetchingEntities) {
                this.loading();
            } else {
                this.setState({ entities: [], isAsyncCall: false });
            }
        }
    }

    componentWillUnmount() {
        this.setState({ entities: [] });
    }

    render() {
        const { builder, mapCoordinates } = this.props;
        return <div className="entity_displayer">
            {this.state.isAsyncCall ? <CardsDisplayer
                itemCount={30}
                itemBuilder={(index) => {
                    return <div className='shine' />;
                }}
                onReachBottom={async () => { }}
            /> : <CardsDisplayer
                itemCount={this.state.entities.length}
                itemBuilder={(index) => {
                    const entity = this.state.entities[index];
                    return builder(entity);
                }}
                onReachBottom={async () => {
                    if (!this.state.maxReached) {
                        this.loading();
                    }
                }}
                mapCoordinates={mapCoordinates(this.state.entities)}
            />

            }
        </div>
    }
}
