import React from "react";
import './cards_displayer.css';
import EmbeddedMap from "../map/map";
import { Fragment } from "react";



export default class CardsDisplayer extends React.Component {

    state = {
        items: [],
    }

    builder({ itemCount, itemBuilder }) {
        return Array.from({ length: itemCount }, (_, i) => {
            return itemBuilder(i);
        })
    }

    handleScroll = (event) => {
        const { scrollTop, scrollHeight, clientHeight } = event.target;
        if (scrollHeight - scrollTop === clientHeight) {
            this.props.onReachBottom();
        }
    }

    componentDidMount() {
        this.setState({ items: this.builder({ itemCount: this.props.itemCount, itemBuilder: this.props.itemBuilder }) })
    }

    componentDidUpdate(prevProps) {
        if (this.props.itemCount !== prevProps.itemCount && this.props.itemBuilder !== prevProps.itemBuilder) {
            this.setState({ items: this.builder({ itemCount: this.props.itemCount, itemBuilder: this.props.itemBuilder }) })
        }
    }

    render() {
        const isLoading = typeof this.props.mapCoordinates === 'undefined';
        return <>
            {!this.state.isMapView ? <div className='cards_displayer_wrapper' onScroll={this.handleScroll} style={isLoading ? { overflow: 'hidden' } : {}}>
                {this.state.items.map((e, index) => { return <Fragment key={index}>{e}</Fragment> })}
            </div> : <div style={{ height: '100%', width: '100%' }}><EmbeddedMap markerCount={[...this.props.mapCoordinates].length} coordinatesBuilder={(index) => {
                return [...this.props.mapCoordinates][index];
            }} /></div>}
            {this.props.mapCoordinates ? <div id="view_type_button_holder_div" className='view_type_button_holder action_button flex' onClick={(event) => {
                event.stopPropagation();
                this.setState({ isMapView: !this.state.isMapView })
            }}>
                <i className={`fa fa-${this.state.isMapView ? "th-large" : "map-location-dot"}`} />
                <h6>{this.state.isMapView ? "Grid" : "Map"}</h6>
            </div> : <></>}

        </>
    }
}