import React from "react";
import { connect } from "react-redux";
import { systemAuthBaseUrl } from "../../../config/config"; 
import {appName} from "../../../config/config";
import ApiProvider, { ApiMethods } from "../../../data/providers/api_services";
import './pending_intents.css';
import { ShimmerTable } from "react-shimmer-effects";
// import moment from 'moment';

class PendingPaymentIntents extends React.Component {

    state = {
        intents: [],
        isAsyncCall: false,
    }

    componentDidMount() {
        if (this.props.items.length) {
            this.setState({ intents: this.props.items })

        } else {
            this.onFetchIntents();
        }
    }

    render() {
        return <div className="pending_payment_intents">
            <div className="pending_payment_intents_table">
                {this.state.isAsyncCall ? <div><ShimmerTable row={0} col={6} /><ShimmerTable row={0} col={6} /><ShimmerTable row={0} col={6} /><ShimmerTable row={0} col={6} /></div> : <>
                    <div className="pending_payment_intents_table_table_row">
                        <div className="pending_payment_intents_table_table_row_column pending_payment_intents_table_table_row_column_title">DATE</div>
                        <div className="pending_payment_intents_table_table_row_column pending_payment_intents_table_table_row_column_title">REFERENCE NO</div>
                        <div className="pending_payment_intents_table_table_row_column pending_payment_intents_table_table_row_column_title">SUBSCRIPTION</div>
                        <div className="pending_payment_intents_table_table_row_column pending_payment_intents_table_table_row_column_title">AMOUNT</div>
                        <div className="pending_payment_intents_table_table_row_column pending_payment_intents_table_table_row_column_title pending_payment_intents_table_table_row_column_last">ACTION</div>
                    </div>
                    <div className="pending_payment_intents_table_body">
                        {[...this.state.intents].reverse().map((e, i) => {
                            return <div key={e.id} className="pending_payment_intents_table_table_row pending_payment_intents_table_table_row_tuple">
                                <div className="pending_payment_intents_table_table_row_column">{new Date(e.createdAt).toLocaleString()}</div>
                                <div className="pending_payment_intents_table_table_row_column">{e.CodeRef}</div>
                                <div className="pending_payment_intents_table_table_row_column">{e.SubscriptionType.Name}</div>
                                <div className="pending_payment_intents_table_table_row_column">${e.SubscriptionType.Price}</div>
                                <div className="pending_payment_intents_table_table_row_column pending_payment_intents_table_table_row_column_last"><span className="delete_action_p_i" onClick={(event) => {
                                    event.stopPropagation();
                                    this.onDeleteIntents(e.id);
                                }}>Cancel</span></div>
                            </div>
                        })}
                    </div>
                </>}
            </div>
        </div>
    }

    onFetchIntents() {
        this.setState({ isAsyncCall: !this.state.isAsyncCall })
        const restApiServices = new ApiProvider();
        return restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: "api/bank-intent-view-list",
            apiMethod: ApiMethods.get,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: localStorage.getItem(`${appName}act`) ?? ''
                }
            },
        }).then((response) => {
            if (typeof (response.error) === "undefined") {
                const intents = response.response;
                this.setState({ intents: intents, isAsyncCall: !this.state.isAsyncCall })
                this.props.onItems(intents);
            } else {
                this.setState({ intents: [], isAsyncCall: !this.state.isAsyncCall })
                this.props.onItems([]);
            }
        });
    };

    onDeleteIntents(id) {
        this.setState({ isAsyncCall: !this.state.isAsyncCall })
        const restApiServices = new ApiProvider();
        return restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: `api/bank-intent-delete/${id}`,
            apiMethod: ApiMethods.delete,
            successCodeExpectation: 200,
            requestData: {
                token: { accessToken: localStorage.getItem(`${appName}act`) ?? '' },
                body: {},
            },
        }).then((response) => {
            if (typeof (response.error) === "undefined") {
                const newIntents = this.state.intents.filter((e) => e.id !== id);
                this.setState({ intents: newIntents, isAsyncCall: !this.state.isAsyncCall })
                this.props.onItems(newIntents);
            } else {
                this.setState({ subscriptionHistory: [], isAsyncCall: !this.state.isAsyncCall })
                this.props.onItems([]);
            }
        });
    };
}

const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage,
    userInformation: storeOf.madoshome.userInformation,
    SubscriptionTypeList: storeOf.madoshome.SubscriptionTypeList,
});

export default connect(mapStateToProps)(PendingPaymentIntents)