import React, { createRef } from "react";
import BedRoomsInput from "../../components/bed_room_input/bed_room_input";
import FileUploaderInput from "../../components/image_input/file_uploader_input";
import LocationPickerInput from "../../components/location_picker/location_picker";
import MultiSelectInput from "../../components/multi_select_input/multi_select_input";
import Joi from "joi";
import Notifier from "../../components/notifier/notifier";
import { capitalize, getErrorMessage, isEmpty, amenities } from "../../../utils/utils";
import { connect } from "react-redux";
import ApiProvider, { ApiMethods } from "../../../data/providers/api_services";
import { appName, systemHousesBaseUrl } from "../../../config/config"; 



class PublishPropertyForm extends React.Component {

    state = {
        isAsyncCall: false,
        isAsyncFileUploadingCall: false,
        formValues: {},
        errorMessage: '',
        successMessage: '',
        isFileUploadTime: false,
        imageUploadSuccess: false,
        videoUploadSuccess: false,
        propertyTypes: [],
        noSubscription: false,
        isResetTime: false,
    }

    joiSchema = Joi.object({
        BuildingTypeId: Joi.number().min(0).required(),
        Latitude: Joi.number().not(0.0).required(),
        Longitude: Joi.number().not(0.0).required(),
        Address: Joi.string().required(),
        LivingRoom: Joi.number().min(0).required(),
        DinningRoom: Joi.number().min(0).required(),
        BathRoom: Joi.number().min(0).required(),
        BedRooms: Joi.array().items(Joi.object({
            BathRoom: Joi.boolean().default(false),
            Wardrobe: Joi.boolean().default(false),
            Balcony: Joi.boolean().default(false),
        })).required(),
        Parking: Joi.number().min(0).required(),
        Garage: Joi.number().min(0).required(),
        Kitchen: Joi.number().min(0).required(),
        Office: Joi.number().min(0).required(),
        Balcony: Joi.number().min(0).required(),
        Size: Joi.number().min(0).required(),
        BuiltIn: Joi.number().min(0).required(),
        Sauna: Joi.boolean().required(),
        Wifi: Joi.boolean().required(),
        Laundry: Joi.boolean().required(),
        Garden: Joi.boolean().required(),
        Gym: Joi.boolean().required(),
        SwimmingPool: Joi.boolean().required(),
        Heater: Joi.boolean().required(),
        Jacuzy: Joi.boolean().required(),
        StoreRoom: Joi.boolean().required(),
        AirConditioner: Joi.boolean().required(),
        PetFriendly: Joi.boolean().required(),
        Funished: Joi.boolean().required(),
        Doorbell: Joi.boolean().required(),
        BackupSourceForPowerOutage: Joi.boolean().required(),
        Images: Joi.array().items(Joi.object({
            Url: Joi.string().uri(),
        })),
        Video: Joi.string().uri(),
        Description: Joi.string().required(),
    });



    handleChange = (event) => {
        const { name, value } = event.target;
        this.setState(state => ({ ...state, formValues: { ...state.formValues, [name]: value } }));
    }

    async handleSubmit({ imageUploadSuccess = this.state.imageUploadSuccess, videoUploadSuccess = this.state.videoUploadSuccess }) {
        await this.setState({ imageUploadSuccess: imageUploadSuccess, videoUploadSuccess: videoUploadSuccess });
        if (this.state.videoUploadSuccess && this.state.imageUploadSuccess) {
            this.onPublishHouses(this.state.formValues)
        }
    }

    formRef = createRef();


    componentDidMount() {
        if (this.props.propertyTypes.length) {
            this.setState({ propertyTypes: this.props.propertyTypes })
        } else {
            this.onFetchPropertyTypes();
        }
    }

    render() {
        const startYear = new Date().getFullYear() - 100;
        return <fieldset className="form_for_publish" disabled={this.state.isAsyncCall}>
            {!isEmpty(this.state.errorMessage) && <>
                <Notifier type='error' message={this.state.errorMessage} />
                <div className="divider" />
            </>}
            {!isEmpty(this.state.successMessage) && <>
                <Notifier type='success' message={this.state.successMessage} />
                <div className="divider" />
            </>}
            <form ref={this.formRef} onChange={this.handleChange} onSubmit={(event) => {
                event.preventDefault();
                console.log(this.state.formValues);
                this.setState({ errorMessage: '', successMessage: '' });
                this.joiSchema.validateAsync(this.state.formValues).then((formValues) => {
                    if (typeof this.state.Images !== 'undefined' && this.state.Images.length > 0) {
                        if (this.state.imageUploadSuccess && this.state.videoUploadSuccess) {
                            this.handleSubmit(true, true);
                        } else {
                            this.setState({ isFileUploadTime: true, isAsyncCall: true });
                        }
                    } else {
                        this.setState({ errorMessage: "At least one photo of the property should be provided." });
                    }
                }).catch((error) => {
                    this.setState({ errorMessage: `${error.details.map((e) => e.message)}` });
                });
            }}>
                <select required={true} className="account_type" name="BuildingTypeId" defaultValue={0}>
                    <option value={0} disabled={true}>Type</option>
                    {this.state.propertyTypes.map((e, i) => {
                        return <option key={i} value={e.id}>{e[capitalize(this.props.currentLanguage.code)]}</option>
                    })}
                </select>
                <LocationPickerInput onChange={(lat, lng) => {
                    this.setState(state => ({ ...state, formValues: { ...state.formValues, Latitude: lat, Longitude: lng } }));
                }} />
                <input type="text" name="Address" placeholder="Full address" />
                <div className='row_inputs flex'>
                    <input type="number" name="LivingRoom" placeholder="Livingrooms" />
                    <input type="number" name="DinningRoom" placeholder="Dinnerrooms" />
                </div>
                <input type="number" name="BathRoom" placeholder="Bathrooms" />
                <BedRoomsInput onChange={(rooms) => {
                    this.setState(state => ({ ...state, formValues: { ...state.formValues, BedRooms: rooms } }));
                }} />
                <MultiSelectInput placeholder="Amenities" items={amenities.map((e) => e.name)} onConfirm={(selectedIndex) => {
                    var amenitiesForDb = {};
                    amenities.forEach((amenity, index) => {
                        amenitiesForDb = { ...amenitiesForDb, [amenity.key]: [...selectedIndex].filter((e) => e === index).length > 0 };
                    });
                    this.setState(state => ({ ...state, formValues: { ...state.formValues, ...amenitiesForDb } }));
                }} />
                <div className='row_inputs flex'>
                    <input type="number" name="Parking" placeholder="Parking" />
                    <input type="number" name="Garage" placeholder="Garage" />
                </div>
                <div className='row_inputs flex'>
                    <input type="number" name="Balcony" placeholder="Balcony" />
                    <input type="number" name="Kitchen" placeholder="Kitchen" />
                </div>
                <input type="number" name="Office" placeholder="Office" />
                <div className='row_inputs flex'>
                    <input type="number" name="Size" placeholder="Floor size (m2)" />
                    <select required={true} className="account_type" name="BuiltIn" defaultValue={0}>
                        <option value={0} disabled={true}>Built in</option>
                        {Array.from({ length: new Date().getFullYear() - startYear }, (_, i) => {
                            return new Date().getFullYear() - i;
                        }).map((e, i) => {
                            return <option key={i} value={Number(e)}>{e}</option>
                        })}
                    </select>
                </div>

                <FileUploaderInput
                    hintText="Photos (15 photos max)"
                    multiple={true}
                    extensions={['jpeg', 'jpg', 'png']}
                    max={15}
                    onChange={(files) => {
                        this.setState({ Images: files, isFileUploadTime: false });
                    }}
                    uploadOptions={{
                        userUUID: this.props.userInformation.UID,
                        field: 'Uploaded-houses',
                        itsUploadTime: this.state.isFileUploadTime,
                        onUploadSuccess: (urls) => {
                            this.setState(state => ({ ...state, formValues: { ...state.formValues, Images: [...urls].filter((e) => !isEmpty(e)).map((e) => { return { Url: e } }) } }));
                            this.handleSubmit({ imageUploadSuccess: true });
                        }
                    }}
                />
                <FileUploaderInput
                    hintText="Video (100 mb file size max)"
                    multiple={false}
                    extensions={['webm', 'mp4']}
                    max={1}
                    onChange={(files) => { }}
                    uploadOptions={{
                        userUUID: this.props.userInformation.UID,
                        field: 'Uploaded-houses',
                        itsUploadTime: this.state.isFileUploadTime,
                        onUploadSuccess: (urls) => {
                            this.setState(state => ({ ...state, formValues: { ...state.formValues, Video: [...urls][0] } }));
                            this.handleSubmit({ videoUploadSuccess: true });
                        }
                    }}
                />
                <textarea name='Description' placeholder="Description" />
                <div className="divider" />
                {!isEmpty(this.state.errorMessage) && <>
                    <Notifier type='error' message={this.state.errorMessage} />
                    <div className="divider" />
                </>}
                {!isEmpty(this.state.successMessage) && <>
                    <Notifier type='success' message={this.state.successMessage} />
                    <div className="divider" />
                </>}
                <button className={`${this.state.isAsyncCall && 'button_on_async'}`} type={'submit'}>{this.state.isAsyncCall ? 'Please wait...' : "Publish"}</button>
            </form>
        </fieldset>
    }

    onPublishHouses(house) {
        this.setState({ isAsyncCall: true });
        const restApiServices = new ApiProvider();
        return restApiServices.provider({
            baseUrl: systemHousesBaseUrl,
            endpoint: "api/building/insert",
            apiMethod: ApiMethods.post,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: localStorage.getItem(`${appName}act`) ?? ''
                },
                body: house,
            },
        }).then((response) => {
            if (typeof (response.error) === "undefined") {
                this.formRef.current.reset();
                this.setState({ isFileUploadTime: false, isAsyncCall: false, successMessage: "The house has been published successfully" });
            } else {
                this.setState({ isAsyncCall: false, errorMessage: getErrorMessage(response.error, 'en') });
            }
        });
    };

    onFetchPropertyTypes() {
        this.setState({ isAsyncCall: true });
        const restApiServices = new ApiProvider();
        return restApiServices.provider({
            baseUrl: systemHousesBaseUrl,
            endpoint: "api/utils",
            apiMethod: ApiMethods.get,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: localStorage.getItem(`${appName}act`) ?? ''
                }
            },
        }).then((response) => {
            if (typeof (response.error) === "undefined") {
                const propertyTypes = response.response.BuildingType;
                this.setState({ propertyTypes: propertyTypes, isAsyncCall: false });
                this.props.onPropertyTypes(propertyTypes);
            } else {
                this.setState({ propertyTypes: [], isAsyncCall: false, errorMessage: getErrorMessage(response.error, 'en') });
                this.props.onPropertyTypes([]);
            }
        });
    };
}

const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage,
    userInformation: storeOf.madoshome.userInformation,
});

export default connect(mapStateToProps)(PublishPropertyForm);