export function uploadFilesS3(file, { userUUID = getRandomArbitrary(1000000, 1000000000), field = 'Unspecified' }) {
    const date = new Date();

    return uploadFile(file, {
        bucketName: process.env.REACT_APP_S3_BUCKET_NAME,
        region: process.env.REACT_APP_S3_BUCKET_REGION,
        accessKeyId: process.env.REACT_APP_S3_ACCESS_KEY,
        secretAccessKey: process.env.REACT_APP_S3_SECRET_KEY,
        dirName: `${appName}/${userUUID}/${field}/${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`,
    })
        .then((data) => {
            console.log(data);
            return encodeURI(decodeURI(data.location));
        })
        .catch((err) => {
            console.log(err);
            return '';
        });
}

export function deleteFilesS3(fileUrl) {
    const getFilePath = String(fileUrl).replace(`https://${process.env.REACT_APP_S3_BUCKET_NAME}.s3.amazonaws.com`, '');
    const splittedPath = String(getFilePath).split("/");
    const fileName = splittedPath[(splittedPath.length - 1)];
    const dirName = String(getFilePath).replace(`/${fileName}`, '');
    return deleteFile(fileName, {
        bucketName: process.env.REACT_APP_S3_BUCKET_NAME,
        region: process.env.REACT_APP_S3_BUCKET_REGION,
        accessKeyId: process.env.REACT_APP_S3_ACCESS_KEY,
        secretAccessKey: process.env.REACT_APP_S3_SECRET_KEY,
        dirName: dirName,
    })
        .then((data) => {
            console.log(data);
            return true;
        })
        .catch((err) => {
            console.log(err);
            return false;
        });
}

