import React, { Component } from 'react'
import Numeral from 'react-numeral';
import { connect } from 'react-redux';
import { CalcCrow, capitalize } from '../../../utils/utils';
import RawTitleValue from '../title_value/title_value';

class HouseDetails extends Component {
    render() {
        const { building } = this.props;
        return (
            <div>
                <div className='houses_card_details_price_like flex'>
                    {building.SaleDetail && <div className='hotel_card_details_star'>
                        <h3>Sale price</h3>
                        <p>{this.props?.currencies.filter((e) => e.id === building.SaleDetail.Currency)[0]?.Name} <Numeral value={building.SaleDetail.Price} format={'0.0a'} /></p>
                    </div>}
                    {building.Rental && <div className='hotel_card_details_star'>
                        <h3>Rental price</h3>
                        <p>{this.props?.currencies.filter((e) => e.id === building.Rental.Currency)[0]?.Name}  <Numeral value={building.Rental.Price} format={'0.0a'} />/{String(building.Rental.RentalType[capitalize(this.props.currentLanguage.code)]).toLocaleLowerCase()}</p>
                    </div>}
                </div>
                <RawTitleValue title='Type' value={`${building.BuildingType[capitalize(this.props.currentLanguage.code)]}`} />
                <RawTitleValue title='Distance from your location' value={`${CalcCrow(building.Latitude, building.Longitude, this.props.userLocation?.lat, this.props.userLocation?.lon)} Kilometers away`} />
                <RawTitleValue title='Address' value={`${building.Address}`} />
                <RawTitleValue title='Bedrooms' value={building.BedRooms.length} />
                <RawTitleValue title='Bathroom' value={`${building.BathRoom}`} />
                <RawTitleValue title='Livingrooms' value={`${building.LivingRoom}`} />
                <RawTitleValue title='Dinningrooms' value={`${building.DinningRoom}`} />
                <RawTitleValue title='Kitchen' value={`${building.Kitchen}`} />
                <RawTitleValue title='Parking' value={`${building.Parking}`} />
                <RawTitleValue title='Garage' value={`${building.Garage}`} />
                <RawTitleValue title='Office' value={`${building.Office}`} />
                <RawTitleValue title='Balcony' value={`${building.Balcony}`} />
                <RawTitleValue title='Floor size' value={`${building.Size} m2`} />
                <RawTitleValue title='Built in' value={`${building.BuiltIn}`} />
                <RawTitleValue title='Funished' value={building.Funished ? '✓' : '✘'} />
                <RawTitleValue title='Sauna' value={building.Sauna ? '✓' : '✘'} />
                <RawTitleValue title='Laundry' value={building.Laundry ? '✓' : '✘'} />
                <RawTitleValue title='Swimming pool' value={building.SwimmingPool ? '✓' : '✘'} />
                <RawTitleValue title='Jacuzzy' value={building.Jacuzy ? '✓' : '✘'} />
                <RawTitleValue title='Garden' value={building.Garden ? '✓' : '✘'} />
                <RawTitleValue title='Air conditioner' value={building.AirConditioner ? '✓' : '✘'} />
                <RawTitleValue title='Wi-Fi' value={building.Wifi ? '✓' : '✘'} />
                <RawTitleValue title='Doorbell' value={building.Doorbell ? '✓' : '✘'} />
                <RawTitleValue title='Heater' value={building.Heater ? '✓' : '✘'} />
                <RawTitleValue title='Gym' value={building.Gym ? '✓' : '✘'} />
                <RawTitleValue title='Storeroom' value={building.StoreRoom ? '✓' : '✘'} />
                <RawTitleValue title='Backup source on power outage' value={building.BackupSourceForPowerOutage ? '✓' : '✘'} />
                <RawTitleValue title='Pet friendly' value={building.AirConditioner ? '✓' : '✘'} />
                <div className='divider' />
                <p>{building.Description}</p>
                <div className='divider' />
            </div>
        )
    }
}


const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage,
    currencies: storeOf.madoshome.currencies,
    userLocation: storeOf.madoshome.userLocation,
});

export default connect(mapStateToProps)(HouseDetails);
