import React from "react";
import FileUploaderInput from "../../components/image_input/file_uploader_input";
import LocationPickerInput from "../../components/location_picker/location_picker";
import MultiSelectInput from "../../components/multi_select_input/multi_select_input";
import Joi from "joi";
import Notifier from "../../components/notifier/notifier";
import { isEmpty } from "../../../utils/utils";



export default class PublishHotelForm extends React.Component {

    state = {
        isForSale: false,
        isAsyncCall: false,
        formValues: {},
        errorMessage: '',
        successMessage: '',
        infoMessage: '',
    }

    getJoiSchemaValues(isForSale) {
        const Schema = {
            PropertyType: Joi.number().required(),
            Coordinates: Joi.object().keys({
                Latitude: Joi.number().not(0.0).required(),
                Longitude: Joi.number().not(0.0).required(),
            }).required(),
            FullAddress: Joi.string().required(),
            Price: Joi.number().required(),
            Currency: Joi.number().required(),
            LivingRooms: Joi.number().required(),
            DinnerRooms: Joi.number().required(),
            BathRooms: Joi.number().required(),
            BedRooms: Joi.array().items(Joi.object({
                BathRoom: Joi.boolean().default(false),
                Wardrobe: Joi.boolean().default(false),
                Balcony: Joi.boolean().default(false),
            })).required(),
            Parking: Joi.number().required(),
            Garage: Joi.number().required(),
            FloorSize: Joi.number().required(),
            BuiltIn: Joi.number().required(),
            Sauna: Joi.boolean().required(),
            Laundry: Joi.boolean().required(),
            Garden: Joi.boolean().required(),
            Kitchen: Joi.boolean().required(),
            Gym: Joi.boolean().required(),
            SwimmingPool: Joi.boolean().required(),
            Heater: Joi.boolean().required(),
            Jacuzi: Joi.boolean().required(),
            StoreRoom: Joi.boolean().required(),
            AirConditioner: Joi.boolean().required(),
            PetFriendly: Joi.boolean().required(),
            Funished: Joi.boolean().required(),
            Balcony: Joi.boolean().required(),
            DoorBell: Joi.boolean().required(),
            Photos: Joi.array().items(Joi.string().dataUri()).required(),
            Video: Joi.string().dataUri().required(),
        };
        return isForSale ? Schema : {
            ...Schema,
            RentalTerm: Joi.number().required(),
        }
    }

    amenities = [
        { key: "Sauna", name: "Sauna" },
        { key: "Laundry", name: "Laundry" },
        { key: "Garden", name: "Garden" },
        { key: "Kitchen", name: "Kitchen" },
        { key: "Gym", name: "Gym" },
        { key: "SwimmingPool", name: "Swimming pool" },
        { key: "Heater", name: "Heater" },
        { key: "Jacuzi", name: "Jacuzi" },
        { key: "StoreRoom", name: "Store room" },
        { key: "AirConditioner", name: "Air conditioner" },
        { key: "PetFriendly", name: "Pet friendly" },
        { key: "Funished", name: "Funished" },
        { key: "Balcony", name: "Balcony" },
        { key: "DoorBell", name: "Doorbell" },
    ];

    handleChange = (event) => {
        const { name, value } = event.target;
        this.setState(state => ({ ...state, formValues: { ...state.formValues, [name]: value } }));
    }

    render() {
        const joiSchema = Joi.object(this.getJoiSchemaValues(this.state.isForSale));
        return <fieldset disabled={this.state.isAsyncCall}>
            {!isEmpty(this.state.errorMessage) && <Notifier type='error' message={this.state.errorMessage} />}
            {!isEmpty(this.state.successMessage) && <Notifier type='success' message={this.state.successMessage} />}
            {!isEmpty(this.state.infoMessage) && <Notifier type='info' message={this.state.infoMessage} />}
            <form className='search_form' onChange={this.handleChange} onSubmit={(event) => {
                event.preventDefault();
                this.setState({ errorMessage: '', successMessage: '', infoMessage: '' });
                joiSchema.validateAsync(this.state.formValues).then((formValues) => {
                    console.log(formValues);
                    // this.onAuth(this.state.formValues);
                }).catch((error) => {
                    this.setState({ errorMessage: `${error.details.map((e) => e.message)}` });
                });
            }}>
                <input type="text" name="Name" placeholder='Name' />
                <LocationPickerInput onChange={(lat, lng) => {
                    this.setState(state => ({ ...state, formValues: { ...state.formValues, Coordinates: { Latitude: lat, Longitude: lng } } }));
                }} />
                <input type="text" name="FullAddress" placeholder="Full address" />
                <div className='row_inputs flex'>
                    <input type="email" name="Email" placeholder="Email" />
                    <input type="tel" name="Phone" placeholder="Phone" />
                </div>
                <input type="text" name="Website" placeholder="Website" />
                <div className='row_inputs flex'>
                <select required={true} className="account_type" name="Language" defaultValue={0}>
                        <option value={0} disabled={true}>Language</option>
                        {["English", "French", "Swahili", "Chinese", 'Lingala'].map((e, i) => {
                            return <option key={i} value={i + 1}>{e}</option>
                        })}
                    </select>
                    <select required={true} className="account_type" name="Currency" defaultValue={0}>
                        <option value={0} disabled={true}>Currency</option>
                        {["USD", "BIF", "CDF", "EURO", 'TSH'].map((e, i) => {
                            return <option key={i} value={i + 1}>{e}</option>
                        })}
                    </select>
                </div>
                <MultiSelectInput placeholder="Amenities" items={this.amenities.map((e) => e.name)} onConfirm={(selectedIndex) => {
                    var amenitiesForDb = {};
                    this.amenities.forEach((amenity, index) => {
                        amenitiesForDb = { ...amenitiesForDb, [amenity.key]: [...selectedIndex].filter((e) => e === index).length > 0 };
                    });
                    this.setState(state => ({ ...state, formValues: { ...state.formValues, ...amenitiesForDb } }));
                }} />
                <FileUploaderInput
                    inputId='housePhot'
                    hintText="Photos"
                    multiple={true}
                    extensions={['jpeg', 'jpg', 'png']}
                    max={15}
                    onChange={(files) => { }}
                />
                <FileUploaderInput
                    inputId='houseVideo'
                    hintText="Video"
                    multiple={false}
                    extensions={['mp4', 'webm']}
                    max={1}
                    onChange={(files) => { }}
                />
                <textarea name='Description' placeholder="Description"/>
                <div className="divider" />
                <button className={`${this.state.isAsyncCall && 'button_on_async'}`} type={'submit'}>{this.state.isAsyncCall ? 'Please wait...' : "Publish"}</button>
            </form>
        </fieldset>
    }
}