import React from "react";
import { systemAuthBaseUrl } from "../../../config/config";
import ApiProvider, { ApiMethods } from "../../../data/providers/api_services";
import { getErrorMessage, isEmpty } from "../../../utils/utils";
import Notifier from "../../components/notifier/notifier";
import './verify_mail.css';
import Joi from "joi";



export default class AuthVerifyMail extends React.Component {
    state = {
        isAsyncCall: false,
        errorMessage: '',
        successMessage: '',
        generatedCode: 0,
        formValues: {},
    }


    componentDidMount() {
        this.onSendingMail();
    }

    handleChange = (event) => {
        const { name, value } = event.target;
        this.setState(state => ({ ...state, formValues: { ...state.formValues, [name]: value } }));
    }


    render() {
        const joiSchema = Joi.object({ UserCode: Joi.number().required() });
        return <div className="modal_rideau">
            <div id="forget_password" className="dif_modal">
                <div className="flex call_to_subcsription_header update_user_informations_header">
                    <div>
                        <h3>Email verification</h3>
                        <p>A verification code has been sent to <span>{this.props.email}</span> Consult the inbox to verify your account.</p>
                    </div>
                    <span id='cross_container' onClick={() => { this.props.onClosePressed() }}>
                        <i className="fa-solid fa-xmark"></i>
                    </span>
                </div>
                <div className="divider" />
                {!isEmpty(this.state.errorMessage) && <Notifier type='error' message={this.state.errorMessage} />}
                {!isEmpty(this.state.successMessage) && <Notifier type='success' message={this.state.successMessage} />}
                <div className='divider' />
                <fieldset disabled={this.state.isAsyncCall}>
                    <form className="flex" onChange={this.handleChange} onSubmit={(event) => {
                        event.preventDefault();
                        this.setState({ errorMessage: '', successMessage: '', infoMessage: '' });
                        joiSchema.validateAsync(this.state.formValues).then((formValues) => {
                            this.onVerifyCode(formValues.UserCode);

                        }).catch((error) => {
                            this.setState({ errorMessage: `${error.details.map((e) => e.message)}` });
                        });
                    }}>
                        <input type="number" name="UserCode" placeholder={this.state.isAsyncCall ? 'Wait...' : 'Verification code'} />
                        <button className={`${this.state.isAsyncCall && 'button_on_async'}`} type={'submit'}>{this.state.isAsyncCall ? 'Wait...' : "Verify"}</button>
                    </form>
                </fieldset>
            </div>
        </div>
    }

    onSendingMail() {
        this.setState({ isAsyncCall: true });
        const restApiServices = new ApiProvider();
        restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: 'api/mail-checker',
            apiMethod: ApiMethods.post,
            successCodeExpectation: 200,
            requestData: {
                token: {},
                body: {
                    Email: this.props.email,
                }
            },
        }).then((response) => {
            if (typeof (response.error) === 'undefined') {
                this.setState({ isAsyncCall: false });
                console.log(response.response);
            } else {
                console.log(response.error);
                this.setState({ isAsyncCall: false, errorMessage: getErrorMessage("e13", 'en') });
            }
        });
    }

    onVerifyCode(code) {
        this.setState({ isAsyncCall: true });
        const restApiServices = new ApiProvider();
        restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: 'api/mail-code-verification',
            apiMethod: ApiMethods.post,
            successCodeExpectation: 200,
            requestData: {
                token: {},
                body: {
                    Email: this.props.email,
                    Code: code,
                }
            },
        }).then((response) => {
            if (typeof (response.error) === 'undefined') {
                this.setState({ isAsyncCall: false });
                this.props.onSuccess(this.props.email, code);
            } else {
                console.log(response.error);
                const hasChecker = typeof response.error?.checker !== 'undefined';
                this.setState({ isAsyncCall: false, errorMessage: `${hasChecker ? 'Wrong code' : getErrorMessage(response.error, 'en')}` });
            }
        });
    }
}