import './navigation.css';
import React from 'react';
import { Link } from 'react-router-dom';
import { appName } from '../../../config/config';
import { connect } from 'react-redux';
import Authentication from '../../pages/Authentication/authentication';
import Publish from '../../pages/publish/publish';
import SearchUsers from '../search_users/search_users';
import ThemeChanger from '../theme_changer/theme_changer';
// import Report from '../../pages/my_account/report';




class NavigationMenu extends React.Component {
    state = {
        isClicked: 0,
        isShowSmallMenucontainer: false,
        wannaPublish: false,
        wannaSearch: false,
        isShowLogin: false,
    }
    Menu = [
        { listName: "Home", route: '', IconName: "" },
        // { listName: "Favorite", route: 'favorites', IconName: "" },
        { listName: "Search", route: 'search', IconName: "" },
        { listName: "My Account", route: 'my-account', IconName: "" },
    ];
    options = [
        { IconName: "fa-solid fa-arrow-right-to-bracket", listName: "Join us to unlock the best \tof MadosHome" },
        { IconName: "fa-solid fa-circle-question", listName: "Help" },
        { IconName: "fa-solid fa-circle-info", listName: "Info" },
        { IconName: "fa-solid fa-share-nodes", listName: "Share" }

    ];

    componentDidMount() {
        let currentIndex = localStorage.getItem(`${appName}clm`) ?? 0;
        this.setState({ isClicked: Number(currentIndex) })
    }
    render() {
        const isLogin = Object.keys(this.props.userInformation).length === 0 ? false : true;

        return (
            <nav>
                {this.state.isShowLogin ? <Authentication onClosePressed={() => { this.setState({ isShowLogin: false }) }} /> : <></>}
                {this.state.wannaSearch ? <SearchUsers onClosePressed={() => { this.setState({ wannaSearch: false }) }} /> : <></>}
                {this.state.wannaPublish && <Publish onClosePressed={() => { this.setState({ wannaPublish: !this.state.wannaPublish }) }} />}
                <div className='Logo'>
                    <h2 id='logo'>{appName}</h2>
                </div>
                <ul className='Menu_container'>
                    {this.Menu.map((e, i) => {
                        const isSelected = i === this.state.isClicked;
                        return (
                            <Menu
                                key={i}
                                isClicked={isSelected}
                                route={e.route}
                                listName={e.listName}
                                onPressed={() => {
                                    localStorage.setItem(`${appName}clm`, i);
                                    this.setState({ isClicked: i });
                                }}
                                iconName={e.IconName}
                            />
                        )
                    })}
                </ul>
                <div className='Nav_action'>
                    <i className="fa-solid fa-users-viewfinder" onClick={(e) => { e.stopPropagation(); this.setState({ wannaSearch: true })}}></i>
                    <ThemeChanger/>
                    {/* <i className="fa-solid fa-globe" onClick={(e) => { e.stopPropagation(); }}></i> */}
                    <div className='action_button flex' onClick={(event) => {
                        if (isLogin) {
                            this.setState({ wannaPublish: !this.state.wannaPublish })
                        } else {
                            this.setState({ isShowLogin: !this.state.isShowLogin })
                        }
                        event.stopPropagation()
                    }}>
                        <i className='fa fa-house-laptop' />
                        <h6>Publish</h6>
                    </div>
                    <div className='profil_container' onClick={() => { this.setState({ isShowSmallMenucontainer: !this.state.isShowSmallMenucontainer }) }}>
                        <span id='active_status'></span>
                        <i className="fa-solid fa-bars"  ></i>
                        <i className="fa-solid fa-circle-user" id="profil"></i>
                    </div>
                    {this.state.isShowSmallMenucontainer === true ?
                        <div className='small_menu_container'>
                            {this.options.map((e, i) => {
                                return (
                                    <div key={i}>
                                        {i === 0
                                            ? <> {isLogin ? <>

                                            </> : <>
                                                <div className='small_menu_title'>
                                                    <i className={e.IconName}></i>
                                                    <div id="small_title">
                                                        <span onClick={(event) => {
                                                            event.stopPropagation();
                                                            this.setState({ isShowSmallMenucontainer: false });
                                                            this.props.onDisplayModalogin();
                                                        }}>{e.listName}
                                                        </span>
                                                    </div>
                                                </div>
                                                <hr />
                                            </>
                                            }
                                            </>
                                            : <div className='small_menu'>
                                                <i className={e.IconName}></i>
                                                <li key={i}>{e.listName}</li>
                                            </div>
                                        }
                                    </div>
                                )
                            })}
                            
                        </div>
                        : <></>
                    }
                </div>
            </nav>
        )
    }
}
export function Menu({ iconName, listName, route, onPressed, isClicked }) {
    return (
        <li
            onClick={onPressed}
        >
            <Link
                className={`${isClicked === true && ""}`}
                to={`/${route}`}
                
            >
                {iconName === '' ? <>
                </> :
                    <i className={`fa fa-${iconName}`}></i>
                }
                <h4 style={{color: 'var(--subtitleColor)', fontWeight: '700'}}>{listName}</h4>
            </Link>
        </li>

    );
}
const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage,
    userInformation: storeOf.madoshome.userInformation,
    // SubscriptionTypeList: storeOf.madoshome.SubscriptionTypeList,
});
export default connect(mapStateToProps)(NavigationMenu)