import axios from 'axios';
import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { ShimmerCategoryItem } from 'react-shimmer-effects';
import { systemAuthBaseUrl } from '../../../config/config';
import ApiProvider, { ApiMethods } from '../../../data/providers/api_services';
import { capitalize, getErrorMessage, isEmpty } from '../../../utils/utils';
// import Notifier from '../notifier/notifier';
import './search_users.css';

export default class SearchUsers extends Component {
    state = {
        results: [],
        isAsyncCall: false,
        errorMessage: '',
        isTyping: false,
    }

    timeOuter = setTimeout(() => { }, 0);

    handleSearchInputChange = (event) => {
        event.stopPropagation();
        this.setState({ isTyping: true });
        const { value } = event.target;
        this.source?.cancel('');
        this.source = null;
        clearTimeout(this.timeOuter);
        if (!isEmpty(value)) {
            this.timeOuter = setTimeout(() => {
                this.source = axios.CancelToken.source();
                this.onSearch(value);
            }, 3000);
        } else {
            this.setState({ isTyping: false, isAsyncCall: false });
        }
    }

    searchMe(value) {
        if (!isEmpty(value)) {
            this.timeOuter = setTimeout(() => {
                this.source = axios.CancelToken.source();
                this.onSearch(value);
            }, 1000);
        }
    }

    render() {
        return (
            <div className="modal_rideau search_users_rideau">
                <div className='dif_modal search_users'>
                    <div className='search_users_body'>
                        {/* {!isEmpty(this.state.errorMessage) && <>
                            <Notifier type='error' message={this.state.errorMessage} />
                            <div className="divider" />
                        </>} */}
                        <div className='search_users_body_header'>
                            <input type='search' placeholder='Rechercher...' onChange={this.handleSearchInputChange} />
                            <span id='cross_container' onClick={() => { this.props.onClosePressed() }}>
                                <i className="fa-solid fa-xmark"></i>
                            </span>
                        </div>
                        <div className='search_users_body_bottom'>
                            {(this.state.isAsyncCall || this.state.isTyping) && <div className='search_users_body_loading'>
                                <ShimmerCategoryItem hasImage imageType="circular" imageWidth={50} imageHeight={50} text />
                                {/* <ShimmerCategoryItem hasImage imageType="circular" imageWidth={50} imageHeight={50} text /> */}
                            </div>}
                            {(!this.state.isAsyncCall && this.state.results.length) ? <div className='search_users_body_results'>
                                {this.state.results.map((e, i) => {
                                    return <div key={i} className='search_users_card' onClick={() => { this.props.onClosePressed() }}>
                                        <Link to={`/users/${e.id}`}>
                                            <div className="comments_displayer_comment_card_header flex">
                                                <img src={e.ProfilImage ?? `https://avatars.dicebear.com/api/personas/male/${Math.floor(Math.random() * 4777777770)}.png`} alt="" />
                                                <div style={{ width: '100%' }}>
                                                    <h4>{capitalize(e.Fullname)}</h4>
                                                    <div className="comments_displayer_comment_card_header_footer flex">
                                                        <p className="comments_displayer_comment_card_comment_date">{e.Email}</p>
                                                        <div />
                                                    </div>
                                                </div>
                                            </div>
                                        </Link>
                                    </div>
                                })}
                            </div> : <></>}
                        </div>

                    </div>
                </div>
            </div >
        )
    }

    source = null;

    onSearch(value) {
        this.setState({ isAsyncCall: true, errorMessage: '', isTyping: false });
        const restApiServices = new ApiProvider();
        restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: `api/search?query=${value}`,
            apiMethod: ApiMethods.get,
            successCodeExpectation: 200,
            requestData: {
                cancelToken: this.source.token,
            },
        }).then((response) => {
            if (typeof (response.error) === 'undefined') {
                this.setState({ isAsyncCall: false, results: response.response });
            } else {
                this.setState({ isAsyncCall: false, results: [], errorMessage: getErrorMessage(response.error) });
            }
        });
    }
}
