import React, { Component, createRef } from 'react'
import './caroussel002.css';
import Compressor from 'compressorjs';
import { getErrorMessage, isEmpty, uploadFilesS3, deleteFilesS3 } from '../../../utils/utils';
import Notifier from '../notifier/notifier';
import ApiProvider, { ApiMethods } from '../../../data/providers/api_services';
import Loader from '../loader/loader';


export default class Caroussel002 extends Component {

    state = {
        currentIndex: 0,
        fTaken: 0,
        errorMessage: '',
        isAsyncCall: false,
    }

    componentDidMount() {
        this.setState({ fTaken: this.props.initialValues.length });
    }

    slideRef = createRef();

    render() {
        const sliderGap = 300;
        const inputId = `${this.props.hintText}${Math.random() * 2537126}`;
        const { max, hintText, multiple, initialValues, builder, min, uploadOptions } = this.props;
        const extensions = String(this.props.extensions?.map((e) => `.${e}`));
        return (
            <div className='caroussel002'>
                {!isEmpty(this.state.errorMessage) && <>
                    <Notifier type='error' message={this.state.errorMessage} />
                </>}
                <div className='caroussel002_preview'>
                    {this.state.isAsyncCall && <div className='caroussel002_preview_loader'><Loader /></div>}
                    <div className='caroussel002_preview_actions'>
                        <div className='caroussel002_preview_actions_button' onClick={(event) => {
                            event.stopPropagation();
                            window.open(initialValues[this.state.currentIndex]?.Url)
                        }}>
                            <i className='fa fa-expand' />
                        </div>
                        <div className='caroussel002_preview_actions_button' onClick={(event) => {
                            event.stopPropagation();
                            this.setState({ isAsyncCall: true });
                            this.downloadFunction(initialValues[this.state.currentIndex]?.Url).then((dldd) => {
                                this.setState({ isAsyncCall: false });
                                if (!dldd) {
                                    this.setState({ errorMessage: 'We could not download. try again later' });
                                }
                            });

                        }}>
                            <i className='fa fa-download' />
                        </div>
                        {uploadOptions && <div className='caroussel002_preview_actions_button'>
                            <i className='fa fa-trash' onClick={(event) => {
                                if (initialValues.length <= (min + 1)) {
                                    this.setState({ errorMessage: `At least ${min} files is required.` })
                                } else {
                                    event.stopPropagation();
                                    this.setState({ isAsyncCall: true });
                                    deleteFilesS3(initialValues[this.state.currentIndex]?.Url).then((deleted) => {
                                        this.deletFile(initialValues[this.state.currentIndex]?.id);
                                    });
                                }
                            }} />
                        </div>}
                    </div>
                    {builder(this.state.currentIndex)}
                </div>
                <div ref={this.slideRef} className='caroussel002_items'>
                    <div className='caroussel002_items_icons caroussel002_items_icons_left' onClick={(event) => {
                        event.stopPropagation();
                        this.slideRef.current.scrollLeft -= sliderGap;
                    }}>
                        <i className='fa fa-arrow-left' />
                    </div>
                    {uploadOptions && <>
                        <input type='file' multiple={multiple} id={inputId} hidden={true} accept={extensions} onChange={(event) => {
                            event.stopPropagation();
                            this.setState({ isAsyncCall: false, errorMessage: '' });
                            if (this.state.fTaken >= max) {
                                this.setState({ errorMessage: `You can only pick up to ${max} files.` })
                            } else {
                                const fileListObject = event.target.files;
                                this.setState({ isAsyncCall: false });
                                this.handleChange(Object.keys(fileListObject).map((e, i) => { return fileListObject[i] }));
                            }
                        }} />
                        <label className='caroussel002_items_child add_files_card' htmlFor={inputId}>
                            <i className='fa fa-upload' />
                            <p>{hintText}</p>
                        </label>
                    </>}

                    {initialValues.map((e, i) => {
                        return <div key={i} className='caroussel002_items_child' onClick={(event) => {
                            event.stopPropagation();
                            this.setState({ currentIndex: i });
                        }}>{builder(i)}</div>
                    })}
                    <div className='caroussel002_items_icons caroussel002_items_icons_right' onClick={(event) => {
                        event.stopPropagation();
                        this.slideRef.current.scrollLeft += sliderGap;
                    }}>
                        <i className='fa fa-arrow-right' />
                    </div>
                </div>
            </div>
        )
    }

    async downloadFunction(url) {
        return await fetch(url).then(r => r.blob()).then(data_blob => {
            const imageURL = URL.createObjectURL(data_blob);
            const link = document.createElement('a');
            link.href = imageURL;
            link.download = url.split('/').pop();
            link.click();
            return true;
        }).catch((e) => {
            return false;
        });
    }


    async handleChange(selectdFiles) {
        let numberOfFiles = [...selectdFiles].length;
        const supportedExtensions = this.props.extensions;
        let max = this.props.max;
        if (numberOfFiles > max) {
            return;
        } else {
            for (var i = 0; i < numberOfFiles; i++) {
                const fileData = selectdFiles[i];
                await this.setState({ isAsyncCall: true });
                const splittedItems = String(fileData.name).split(".");
                const fileExtension = splittedItems[(splittedItems.length - 1)];
                let isAllowed = [...supportedExtensions].filter((extension) => String(extension).toLocaleLowerCase() === String(fileExtension).toLocaleLowerCase()).length > 0;
                if (isAllowed) {
                    await new Compressor(fileData, {
                        quality: 0.4,
                        success: (compressedResult) => {
                            this.onCompressFinished(compressedResult, this.state.fTaken, numberOfFiles);
                        },
                        error: (e) => {
                            this.onCompressFinished(fileData, this.state.fTaken, numberOfFiles);
                        }
                    });
                } else {
                    this.setState({ isAsyncCall: false });
                    console.log('Unsupported file format =>', fileExtension)
                }

            }
        }
    }

    async onCompressFinished(file, fTaken, max) {
        this.setState({ fTaken: fTaken + 1 });
        if (file.size * 0.000001 <= 100) {
            uploadFilesS3(file, { userUUID: this.props.uploadOptions.userUUID, field: this.props.uploadOptions.field }).then((url) => {
                if (!isEmpty(url)) {
                    this.uploadFile(url);
                } else {
                    this.setState({ isAsyncCall: false });
                    this.setState({ isAsyncCall: false, errorMessage: "We could not upload your file" });
                }
            });
            if (max === (fTaken + 1)) {
                await this.setState({ isAsyncCall: false });
            }
        } else {
            console.log(file.size, "File too large", file.size * 0.000001, "Mb");
            if (max === (fTaken + 1)) {
                await this.setState({ isAsyncCall: false });
            }
        }
    }

    uploadFile(url) {
        this.setState({ isAsyncCall: true });
        const { baseUrl, uploadEndpoint, token } = this.props.uploadOptions;
        const { onUpload, entityIdObject } = this.props;
        const restApiServices = new ApiProvider();
        restApiServices.provider({
            baseUrl: baseUrl,
            endpoint: `api/${uploadEndpoint}`,
            apiMethod: ApiMethods.post,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: token
                },
                body: {
                    Url: url,
                    ...entityIdObject,
                }
            },
        }).then((response) => {
            if (typeof (response.error) === "undefined") {
                const object = response.response;
                onUpload(object);
                this.setState({ isAsyncCall: false, currentIndex: 0 });
            } else {
                this.setState({ isAsyncCall: false, errorMessage: getErrorMessage(response.error, 'en') });
            }
        });
    };

    deletFile(id) {
        this.setState({ isAsyncCall: true });
        const { baseUrl, deleteEndpoint, token } = this.props.uploadOptions;
        const { onDelete } = this.props;
        const restApiServices = new ApiProvider();
        restApiServices.provider({
            baseUrl: baseUrl,
            endpoint: `api/${deleteEndpoint}/${id}`,
            apiMethod: ApiMethods.delete,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: token
                },
            },
        }).then((response) => {
            if (typeof (response.error) === "undefined") {
                this.setState({ isAsyncCall: false });
                onDelete(id);
            } else {
                this.setState({ isAsyncCall: false, errorMessage: getErrorMessage(response.error, 'en') });
            }
        });
    };
}
