import './coloredbutton.css';
import React from 'react';


function ColoredButton({type,toBeExpended,label}){
    return(
        <>
            <button
                type={type}
                style={toBeExpended === true ? {width:'100%'} : {width:'fit-content'}}
            >{label}</button>
        </>
    )
}

export default (ColoredButton)