import './authentication.css';
import React, { createRef } from 'react';
import ApiProvider, { ApiMethods } from '../../../data/providers/api_services';
import { appName, systemAuthBaseUrl } from '../../../config/config';
import { copyWith } from '../../../logic/slice';
import axios from 'axios';
import { getErrorMessage, isEmpty } from '../../../utils/utils';
import { connect } from 'react-redux';
import Notifier from '../../components/notifier/notifier';
import ManagerRepo from '../../../data/repositories/managerRepo';
import AuthVerifyMail from './verify_mail';
import Joi from "joi";
import ForgetPassword from './forget_password';



class Authentication extends React.Component {

    state = {
        isLogin: false,
        currentMail: '',
        currentPassword: '',
        isAsyncCall: false,
        errorMessage: '',
        successMessage: '',
        infoMessage: '',
        formValues: {},
        isVerifyMailTime: false,
        didForgetPassword: false,
    }

    getJoiSchemaValues(isLogin) {
        const loginFormValues = {
            Username: Joi.string().required(),
            Password: Joi.string().required(),
        };
        return isLogin ? loginFormValues : {
            ...loginFormValues,
            ConfirmPassword: Joi.string().required().valid(Joi.ref('Password')),
            Fullname: Joi.string().required(),
            Email: Joi.string().required().email({ tlds: { allow: false } }),
            PhoneNumber: Joi.string().required(),
            WhatsappNumber: Joi.string().required(),
            UserTypeId: Joi.number().required(),
            ProfilImage: Joi.string().default(`https://avatars.dicebear.com/api/personas/male/${Math.floor(Math.random() * 4777777770)}.png`),
        }
    }

    formRef = createRef();

    handleChange = (event) => {
        const { name, value } = event.target;
        this.setState(state => ({ ...state, formValues: { ...state.formValues, [name]: value } }));
    }

    componentWillUnmount() {
        this.source.cancel();
    }

    render() {
        const joiSchema = Joi.object(this.getJoiSchemaValues(this.state.isLogin));
        return (
            <div className='modal_rideau'>
                <div className="authentication_container dif_modal">
                    {this.state.isVerifyMailTime === true &&
                        <AuthVerifyMail
                            email={this.state.currentMail}
                            onSuccess={() => {
                                this.setState({ isVerifyMailTime: !this.state.isVerifyMailTime });
                                this.onAuth({ ...this.state.formValues, ProfilImage: `https://avatars.dicebear.com/api/personas/male/${Math.floor(Math.random() * 4777777770)}.png` });
                            }}
                            onClosePressed={() => { this.setState({ isVerifyMailTime: !this.state.isVerifyMailTime, infoMessage: 'Email verification is required to proceed with your registration.' }) }}
                        />
                    }
                    {this.state.didForgetPassword === true &&
                        <ForgetPassword onClosePressed={() => { this.setState({ didForgetPassword: !this.state.didForgetPassword, }) }}
                        />
                    }
                    <div className="header_login" >
                        <h3>Join us to unlock the best of Madoshome</h3>
                        <span id='cross_container' onClick={this.props.onClosePressed}>
                            <i className="fa-solid fa-xmark"></i>
                        </span>
                    </div>
                    <div className='divider' />
                    {!isEmpty(this.state.errorMessage) && <Notifier type='error' message={this.state.errorMessage} />}
                    {!isEmpty(this.state.successMessage) && <Notifier type='success' message={this.state.successMessage} />}
                    {!isEmpty(this.state.infoMessage) && <Notifier type='info' message={this.state.infoMessage} />}
                    <fieldset disabled={this.state.isAsyncCall}>
                        <form autoComplete='off' ref={this.formRef} className='form_container' onChange={this.handleChange} onSubmit={(event) => {
                            event.preventDefault();
                            this.setState({ errorMessage: '', successMessage: '', infoMessage: '' });
                            joiSchema.validateAsync(this.state.formValues).then((formValues) => {
                                if (this.state.isLogin) {
                                    this.onAuth(this.state.formValues);
                                } else {
                                    this.setState({ isVerifyMailTime: !this.state.isVerifyMailTime });
                                }
                            }).catch((error) => {
                                this.setState({ errorMessage: `${error.details.map((e) => e.message)}` });
                            });
                        }}>
                            <div className='LR_container'>
                                {!this.state.isLogin && <>
                                    <select className="account_type" name="UserTypeId">
                                        <option value={0}>Account Type</option>
                                        {[...this.props.accountTypes].map((e) => {
                                            return <option key={e.id} value={e.id}>{e.Name}</option>
                                        })}
                                    </select>
                                    <input type="text" name="Fullname" placeholder='Full Name' />
                                    <input
                                        type="email"
                                        name="Email"
                                        placeholder='E-mail'
                                        onChange={(event) => { this.setState({ currentMail: event.target.value }); }}
                                        onBlur={(event) => {
                                            if (!isEmpty(this.state.currentMail)) {
                                                console.log('Verifying...', this.state.currentMail);
                                            }
                                        }} />
                                </>}
                                <input type="text" name="Username" placeholder='Username' />
                                <input type="password" name="Password" placeholder='Password' />
                                {!this.state.isLogin && <>
                                    <input type="password" name="ConfirmPassword" placeholder='Confirm Password' />
                                    <div className="form_input_divider">
                                        <input type="tel" name="PhoneNumber" placeholder='Phone Number' />
                                        <input type="tel" name="WhatsappNumber" placeholder='Whatsapp Number' />
                                    </div>
                                </>}
                            </div>
                            <div className="footer_authentication">
                                <button className={`${this.state.isAsyncCall && 'button_on_async'}`} type={'submit'}>{this.state.isAsyncCall ? 'Please wait...' : this.state.isLogin ? "Login" : "Register"}</button>
                                {this.state.isLogin && <h6 className='forget_password_h6' onClick={(event) => { event.stopPropagation(); this.setState({ didForgetPassword: !this.state.didForgetPassword }) }}>Forgot password?</h6>}
                                <p>{this.state.isLogin ? "Don't you have an account ? " : "Already have an account ? "} <span className='forget_password_h6' onClick={() => { this.setState({ successMessage: '', errorMessage: '', isLogin: !this.state.isLogin, formValues: {} }); this.formRef.current.reset(); }}>{this.state.isLogin ? "Register" : "Login"}</span></p>
                                {!this.state.isLogin && <div className='have_an_account_or_not'>
                                    <p>By continuing, you agree to our <span className='forget_password_h6'>Terms and conditions</span> and <span className='forget_password_h6'>Privacy policies</span></p>
                                </div>}
                            </div>
                        </form>
                    </fieldset>
                </div>
            </div>
        )
    }

    source = axios.CancelToken.source();
    onAuth(payload) {
        this.setState({ isAsyncCall: true, errorMessage: '', successMessage: '', infoMessage: '' });
        const restApiServices = new ApiProvider();
        restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: this.state.isLogin ? 'api/signin' : 'api/signup',
            apiMethod: ApiMethods.post,
            successCodeExpectation: 200,
            cancelToken: this.source.token,
            requestData: {
                body: payload,
            },
        }).then((response) => {
            if (typeof (response.error) === 'undefined') {
                this.formRef.current.reset();
                this.setState({ isAsyncCall: false, successMessage: `Thank you for joining us, Enjoy the best of ${appName}` });
                if (this.state.isLogin) {
                    const { refreshToken, accessToken } = response.response.user;
                    const managerRepo = new ManagerRepo();
                    managerRepo.onSetRefreshToken({ refreshToken: refreshToken ?? '', accessToken: accessToken ?? '' });
                    this.props.dispatch(copyWith({
                        userInformation: response.response.user,
                        refreshToken: refreshToken ?? '',
                        accessToken: accessToken ?? '',
                    }));
                }
            } else {
                console.log(response.error);
                if (this.state.isLogin) {
                    const hasChecker = typeof response.error?.user !== 'undefined';
                    this.setState({ isAsyncCall: false, errorMessage: `${hasChecker ? 'Wrong username or password' : getErrorMessage(response.error, 'en')}` });
                } else {
                    this.setState({ isAsyncCall: false, errorMessage: getErrorMessage(response.error, 'en') });
                }

            }
        });
    }
}

const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage,
    accountTypes: storeOf.madoshome.accountTypes,
});

export default connect(mapStateToProps)(Authentication);