import './history.css'
import React from 'react';
import { connect } from 'react-redux';
import historyImg from '../../../assets/svgs/historyNotFound.svg'
import CallToSubscription from '../call_to_subscription/call_to_subscription';
import ApiProvider, { ApiMethods } from '../../../data/providers/api_services';
import { appName, systemAuthBaseUrl } from '../../../config/config';
import { useSelector } from 'react-redux';
import { ShimmerCategoryItem } from 'react-shimmer-effects';

class SubscriptionHistory extends React.Component {
    state = {
        isDetailsDisplayed: false,
        subscriptionHistory: [],
        isAsyncCall: false,
    }
    componentDidMount() {
        if (this.props.items.length) {
            this.setState({ subscriptionHistory: this.props.items })

        } else {
            this.onFetchSubscription();
        }
    }
    render() {
        // const isexpired = false;

        return (
            <div className="subscription_history_container">
                {this.state.isDetailsDisplayed ?
                    <CallToSubscription onClosePressed={() => { this.setState({ isDetailsDisplayed: !this.state.isDetailsDisplayed }) }} />
                    : <></>}
                <div className="main_history_container">
                    {

                        this.state.isAsyncCall
                            ? <div style={{ backgroundColor: 'var(--cardColor)', padding: '1rem' }}>
                                <ShimmerCategoryItem row={4} col={4} cta={true} />
                                <ShimmerCategoryItem row={4} col={4} cta={true} />
                                <ShimmerCategoryItem row={4} col={4} cta={true} />
                                <ShimmerCategoryItem row={4} col={4} cta={true} />
                                <ShimmerCategoryItem row={4} col={4} cta={true} />
                                <ShimmerCategoryItem row={4} col={4} cta={true} />
                            </div>
                            :
                            this.props.userInformation.SubscriptionId
                                ? <div className="card_history_container">
                                    <div className='action_button card_history_container_button flex' onClick={(event)=>{
                                        event.stopPropagation();
                                        this.setState({ isDetailsDisplayed: !this.state.isDetailsDisplayed });
                                    }}>
                                        <i className='fa fa-crown' />
                                        <h6>Choose a subriction plan</h6>
                                    </div>
                                    {this.state.subscriptionHistory.reverse().map((singlehistory, i) => {
                                        return <Cardhistory
                                            key={i}
                                            subscriptionId={singlehistory.SubscriptionTypeId}
                                            status={!singlehistory.Actif}
                                            expiredAt={singlehistory.To}
                                            suscribeAt={singlehistory.createdAt}
                                        />
                                    })}
                                </div>
                                : <div className='unavailable_history'>
                                    <img src={historyImg} alt="historyerro" width={350} height={350} />
                                    <p>No subscription history was found with your account <i onClick={() => { this.setState({ isDetailsDisplayed: true }) }} >Subscribe !</i></p>
                                </div>
                    }
                </div>
            </div>
        )
    }
    onFetchSubscription() {
        this.setState({ isAsyncCall: !this.state.isAsyncCall })
        const restApiServices = new ApiProvider();
        return restApiServices.provider({
            baseUrl: systemAuthBaseUrl,
            endpoint: "api/history-sp",
            apiMethod: ApiMethods.get,
            successCodeExpectation: 200,
            requestData: {
                token: {
                    accessToken: localStorage.getItem(`${appName}act`) ?? ''
                }
            },
        }).then((response) => {
            if (typeof (response.error) === "undefined") {
                const subscriptions = response.response;
                this.setState({ subscriptionHistory: subscriptions, isAsyncCall: !this.state.isAsyncCall })
                this.props.onItems(subscriptions);
            } else {
                this.setState({ subscriptionHistory: [], isAsyncCall: !this.state.isAsyncCall })
                this.props.onItems([]);
            }
        });
    };
}
export function Cardhistory({ subscriptionId, status, expiredAt, suscribeAt }) {

    const { SubscriptionTypeList } = useSelector((storeOf) => ({
        SubscriptionTypeList: storeOf.madoshome.subscriptionPlans,
    }))
    return (
        <div className="single_card_history">
            <div className="card_history_header">
                <h5>
                    {SubscriptionTypeList.filter((e) => e.id === subscriptionId)[0]?.Name + " "}
                    - {SubscriptionTypeList.filter((e) => e.id === subscriptionId)[0]?.Price} USD
                </h5>
                <span id='cross_container'>
                    <i className="fa-solid fa-crown"></i>
                </span>
            </div>
            <div className="card_details">
                <div className="single_detail_content">
                    <p>Status </p>
                    {/* 'state expired out_of_bound' */}
                    <span className={status ? "expired" : "state"}>
                        <p id={status ? 'expired' : ''}>{status ? "Expired" : "Active"}</p>
                    </span>
                </div>

                <div className="single_detail_content">
                    <p>Subscribe at </p>
                    <h4>{new Date(suscribeAt).toLocaleDateString()}</h4>
                </div>
                <div className="single_detail_content">
                    <p>Date of Expiration</p>
                    <h4>{new Date(expiredAt).toLocaleDateString()}</h4>
                </div>
                <div className="single_detail_content">
                    <p>Posts  </p>
                    <h4>
                        {SubscriptionTypeList.filter((e) => e.id === subscriptionId)[0]?.Post}
                    </h4>
                </div>
            </div>
        </div>

    )

}

const mapStateToProps = (storeOf) => ({
    currentLanguage: storeOf.madoshome.currentLanguage,
    userInformation: storeOf.madoshome.userInformation,
    SubscriptionTypeList: storeOf.madoshome.SubscriptionTypeList,
});

export default connect(mapStateToProps)(SubscriptionHistory)